@light-blue: #337ab7;

body {
  font-family: 'Source Sans Pro', san-serif;
  background-color: rgb(233, 234, 237);
  .content {
    padding-left: 0px;
    padding-right: 0px;
  }
}
h1 {
  font-size: 48px;
}
h2 {
  font-size: 44px;
}
h3 {
  font-size: 40px;
}
h4 {
  font-size: 36px;
}
h5 {
  font-size: 32px;
}
h6 {
  font-size: 28px;
}

.centered {
  margin: auto;
}

.btn-width-300 {
  font-family: inherit;
  font-size: 16px;
  width: 300px;
}

.margin-top-5 {
  margin-top: 5px;
}

.margin-top-20 {
  margin-top: 20px;
}

.padding-zero {
  padding-left: 0px;
  padding-right: 0px;
}

.status {
  height: 50px;
}

.msg {
  font-size: 16px;
  font-family: inherit;
}

.nav {
  li {
    text-align: center;
    width: 23%;
    a {
      padding: 0;
    }
  }

}

.content {
  .title {
    margin-top: 0px;
  }

  .card {
    margin: 0 auto;
    padding: 10px;
    width: 98%;
    background-color: rgb(255, 255, 255);
    border-bottom-color: rgb(208, 209, 213);
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-image-outset: 0px;
    border-image-repeat: stretch;
    border-image-slice: 100%;
    border-image-source: none;
    border-image-width: 1;
    border-left-color: rgb(223, 224, 228);
    border-left-style: solid;
    border-left-width: 1px;
    border-right-color: rgb(223, 224, 228);
    border-right-style: solid;
    border-right-width: 1px;
    border-top-color: rgb(229, 230, 233);
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    border-top-style: solid;
    border-top-width: 1px;

    .login-form {
      margin: 0 auto;
      max-width: 400px;
    }
    .user-name {
      float: left;
    }
    .profile-img {
      max-width: 65px;
      max-height: 65px;
      img {
        width: 60px;
        height: 60px;
        border-radius: 2em;
      }
    }
  }
  
  .card-b {
    width: 96%;
    margin-top: 5px;
    margin-bottom: 5px;
    .group-name {
      width: 100%;
      display: block;
      font-size: 16px;
    }
    .event-name {
      .timestamp {
        float: right;
        color:@light-blue;
      }
    }
    .message {
      padding-top:5px;
      padding-left:5px;
      .profile-img {
        border-radius: 10px;
        margin-right: 5px;
        max-width: 100%;
        img {
          width: 25px;
          height: 25px;
          border-radius: 5px;
        }
      }
      .text {
        float: left;
      }
    }
  }

  .placeholder {
    padding: 20px;
    background-color: rgb(233, 234, 237);
    border-bottom-color: rgb(208, 209, 213);
    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-image-outset: 0px;
    border-image-repeat: stretch;
    border-image-slice: 100%;
    border-image-source: none;
    border-image-width: 1;
    border-left-color: rgb(223, 224, 228);
    border-left-style: solid;
    border-left-width: 1px;
    border-right-color: rgb(223, 224, 228);
    border-right-style: solid;
    border-right-width: 1px;
    border-top-color: rgb(229, 230, 233);
    border-top-left-radius: 3px;
    border-top-right-radius: 3px;
    border-top-style: solid;
    border-top-width: 1px;
  }
}
